/* 모달 오버레이 */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* 모달 컨텐츠 - 크기 제한 제거 */
.modal-content {
  width: 80vw;  /* 뷰포트 너비의 80% */
  max-width: 480px; /* 너무 커지는 걸 방지 */
  height: auto;  /* 높이를 내용에 맞게 자동 조절 */
  max-height: 90vh; /* 화면을 넘지 않도록 설정 */
  background: white;
  border-radius: 10px;
  padding: 40px;
  position: relative;
  display: flex;
  flex-direction: column;
}


/* 닫기 버튼 */
.close-button {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 24px;
  background: none;
  border: none;
  cursor: pointer;
}

/* 제목 스타일 */
h2 {
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  color: #282828;
  margin-bottom: 20px;
}

.modal-form {
  display: flex;
  flex-direction: column; /* 세로 방향 정렬 */
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
}
  

/* 입력 그룹 */
.input-group {
  padding: 20px;
  max-width: 400px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

/* 라벨 스타일 */
.input-group label {
  font-size: 16px;
  font-weight: 500;
  color: #282828;
  margin-bottom: 8px;
}

/* 입력 필드 스타일 */
.input-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #dddddd;
  border-radius: 6px;
  font-size: 16px;
  color: #282828;
}

  
/* 제출 버튼 스타일 */
.submit-button,
.comment-delete-button,
.memory-delete-button {
  max-width: 400px;
  width: 100%;
  height: 50px;
  border-radius: 6px;
  background-color: #282828;
  color: #fafafa;
  font-size: 16px;
  font-weight: 500;
  border: none;
  cursor: pointer;
  align-self: center;
  margin-top: 20px;
}

.submit-button:hover {
  background-color: #1f1f1f;
}

.input-group input.error {
  border-color: red;
}